import React from "react";

// reactstrap components
import { Button, Container } from "reactstrap";

// core components

function LandingPageHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth < 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          "translate3d(0," + windowScrollTop + "px,0)";
      };
      window.addEventListener("scroll", updateScroll);
      return function cleanup() {
        window.removeEventListener("scroll", updateScroll);
      };
    }
  });

  return (
    <>
      <div
        style={{
          backgroundImage:
            "url(" + require("assets/img/header.jpg") + ")",
        }}
        className="page-header"
        data-parallax={true}
        ref={pageHeader}
      >
        <div className="filter" />
        <Container>
          <div className="motto text-center">
            <h1>Laghu.ai</h1>
            Free, light and easy to use AI webapp
            {/* https://sanskritdictionary.com/laghu/195772/1 */}
            <h3>
              With "Laghu" you can now train your own machine learning models online for both Images or Text. 
              
              You can also download framework agnostic model(ONNX) for your use.
              {/* irrespective of the framework(TensorFlow/Pytorch). */}
              {/* with a free, light and easy to use webapp. */}
            </h3>
            <br />
            {/* <Button
              href="https://www.youtube.com/watch?v=dQw4w9WgXcQ"
              className="btn-round mr-1"
              color="neutral"
              target="_blank"
              outline
            >
              <i className="fa fa-play" />
              Watch video
            </Button> */}
            {/* <Button
              href="/index" 
              // Upload url
              className="btn-round mr-1"
              color="neutral"
              // target="_blank"
              outline
            >
              Train your model - should redirect to below section
            </Button> */}
            {/* <Button className="btn-round" color="neutral" type="button" outline>
              Download - remove?
            </Button> */}

            <Button className="btn-round" color="neutral" type="button" outline>
              Scroll down to know more
            </Button>
          </div>
        </Container>
      </div>
    </>
  );
}

export default LandingPageHeader;
